import "./style.css";
import bootstrap from "bootstrap";
import utils from "./utils";
import main from "./main";
import * as THREE from "three";
import { OrbitControls } from "three/examples/jsm/controls/OrbitControls.js";
import { GLTFLoader } from "three/examples/jsm/loaders/GLTFLoader.js";
import { DRACOLoader } from "three/examples/jsm/loaders/DRACOLoader.js";
import * as dat from "dat.gui";
import vs from "./shaders/responsive-bg/vert.glsl";
import fs from "./shaders/responsive-bg/frag.glsl";

// basics
let canvas, scene, camera, renderer, controls, gui;

let shaderMaterial;

const sizes = {
  width: window.innerWidth,
  height: window.innerHeight,
};

const clock = new THREE.Clock();
let previousTime = 0;

/* 
Basic init
*/
const init = () => {
  // Canvas
  canvas = document.querySelector("canvas.webgl");

  // Scene
  scene = new THREE.Scene();
  // scene.background = new THREE.Color(0xe0e0e0);

  /* 
  Mouse
  */

  const mouse = new THREE.Vector2();

  window.addEventListener("mousemove", (_event) => {
    mouse.x = (_event.clientX / sizes.width) * 2 - 1;
    mouse.y = -(_event.clientY / sizes.height) * 2 + 1;
  });

  /* 
  Plane setup
  */
  const geometry = new THREE.PlaneBufferGeometry(
    sizes.width * 0.02,
    sizes.height * 0.02,
    512,
    512
  );

  shaderMaterial = new THREE.ShaderMaterial({
    vertexShader: vs,
    fragmentShader: fs,
    side: THREE.DoubleSide,
    uniforms: {
      u_time: {
        value: 0,
      },
      u_amount: {
        value: 7.5,
      },
      u_mouse: {
        value: mouse,
      },
    },
  });

  gui = new dat.GUI();
  gui
    .add(shaderMaterial.uniforms.u_amount, "value")
    .min(2)
    .max(15)
    .name("amount");

  gui.hide();

  const basicMterial = new THREE.MeshBasicMaterial({
    color: 0xf00,
  });

  const mesh = new THREE.Mesh(geometry, shaderMaterial);

  mesh.rotateX(Math.PI);
  mesh.position.set(0, 0, 0);
  scene.add(mesh);

  /*
   * Lights
   */
  // utils.setupBasicLights(scene);

  /**
   * Camera
   */

  camera = utils.setUpCamera(sizes);
  camera.position.set(0, 0, 10);
  scene.add(camera);

  /* 
  Controls 
  */
  controls = utils.setUpControls(camera, canvas);
  controls.enablePan = false;
  controls.maxDistance = 4;
  controls.minDistance = 3;
  controls.enableRotate = false;

  /**
   * Renderer
   */
  renderer = utils.setUpRenderer(canvas, sizes);

  /* 
  Setup Listners
  */
  utils.setUpEventListners(camera, renderer, sizes);
};

/**
 * Animate
 */
const tick = () => {
  const elapsedTime = clock.getElapsedTime();
  const deltaTime = elapsedTime - previousTime;
  previousTime = elapsedTime;

  shaderMaterial.uniforms.u_time.value = elapsedTime;

  // Update controls
  controls.update();

  // Render
  renderer.render(scene, camera);

  // Call tick again on the next frame
  window.requestAnimationFrame(tick);
};

init();
tick();
